import { useState, useEffect } from "react";
import axios from "axios";
import "../styles/AdminCosts.css";

const AdminCosts = () => {
  const [costs, setCosts] = useState([]);
  const [newCost, setNewCost] = useState({ service: "", rate: "", city: "" });
  const [message, setMessage] = useState("");

  const fetchCosts = async () => {
    try {
      const { data } = await axios.get("/api/admin/services");
      setCosts(data);
    } catch (err) {
      console.error("Error fetching costs", err);
    }
  };

  const handleCostSubmit = async (e) => {
    e.preventDefault();
    try {
      await axios.post("/api/admin/services", newCost, {
        headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
      });
      setMessage("Cost added successfully!");
      setNewCost({ service: "", rate: "", city: "" });
      fetchCosts();
    } catch (err) {
      console.error("Error adding cost", err);
      setMessage("Failed to add cost.");
    }
  };

  useEffect(() => {
    fetchCosts();
  }, []);

  return (
    <div className="admin-costs">
      <header>
        <h1>Manage Service Costs</h1>
        <p>Add and manage cost rates for different services and cities.</p>
      </header>

      {/* Success/Error Message */}
      {message && <div className="message">{message}</div>}

      {/* Form to Add Costs */}
      <section className="form-section">
        <h2>Add New Cost</h2>
        <form onSubmit={handleCostSubmit} className="cost-form">
          <input
            type="text"
            placeholder="Service Name"
            value={newCost.service}
            onChange={(e) =>
              setNewCost({ ...newCost, service: e.target.value })
            }
            required
          />
          <input
            type="number"
            placeholder="Rate"
            value={newCost.rate}
            onChange={(e) => setNewCost({ ...newCost, rate: e.target.value })}
            required
          />
          <input
            type="text"
            placeholder="City"
            value={newCost.city}
            onChange={(e) => setNewCost({ ...newCost, city: e.target.value })}
            required
          />
          <button type="submit" className="submit-button">
            Add Cost
          </button>
        </form>
      </section>

      {/* Existing Costs */}
      <section className="costs-section">
        <h2>Existing Costs</h2>
        <div className="costs-list">
          {costs.map((cost) => (
            <div key={cost.id} className="cost-card">
              <p>
                <strong>Service:</strong> {cost.service}
              </p>
              <p>
                <strong>Rate:</strong> ${cost.rate}
              </p>
              <p>
                <strong>City:</strong> {cost.city}
              </p>
            </div>
          ))}
        </div>
      </section>
    </div>
  );
};

export default AdminCosts;
