import { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import "../styles/AdminPanel.css";

const AdminPanel = () => {
  const [services, setServices] = useState([]);
  const [blogs, setBlogs] = useState([]);
  const [newBlog, setNewBlog] = useState({ title: "", content: "" });

  const fetchServices = async () => {
    try {
      const { data } = await axios.get("/api/admin/services");
      setServices(data);
    } catch (error) {
      console.error("Error fetching services", error);
    }
  };

  const addBlog = async () => {
    try {
      await axios.post("/api/admin/blogs", newBlog, {
        headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
      });
      alert("Blog added successfully");
      setBlogs([...blogs, newBlog]);
      setNewBlog({ title: "", content: "" });
    } catch (error) {
      console.error("Error adding blog", error);
    }
  };

  useEffect(() => {
    fetchServices();
  }, []);

  return (
    <div className="admin-panel">
      {/* Header */}
      <header className="admin-header">
        <h1>Admin Panel</h1>
        <p>Manage services and create engaging blogs for your users.</p>
      </header>

      {/* Services Section */}
      <section className="services-section">
        <h2>Manage Services</h2>
        <ul className="services-list">
          {services.map((service) => (
            <li key={service.id}>
              <strong>{service.name}</strong> - ${service.rate} ({service.city})
            </li>
          ))}
        </ul>
      </section>
      <Link to="/admin/costs" className="navigate-button">
        Manage Service Costs
      </Link>

      {/* Blog Section */}
      <section className="blog-section">
        <h2>Add Blog</h2>
        <form
          className="blog-form"
          onSubmit={(e) => {
            e.preventDefault();
            addBlog();
          }}
        >
          <input
            type="text"
            placeholder="Blog Title"
            value={newBlog.title}
            onChange={(e) => setNewBlog({ ...newBlog, title: e.target.value })}
            required
          />
          <textarea
            placeholder="Blog Content"
            value={newBlog.content}
            onChange={(e) =>
              setNewBlog({ ...newBlog, content: e.target.value })
            }
            required
          />
          <button type="submit" className="add-blog-button">
            Add Blog
          </button>
        </form>
      </section>
    </div>
  );
};

export default AdminPanel;
