import { useState } from "react";
import axios from "axios";
import "../styles/Estimation.css";

const Estimation = () => {
  const [farmSize, setFarmSize] = useState("");
  const [cropType, setCropType] = useState("");
  const [location, setLocation] = useState("");
  const [estimate, setEstimate] = useState(null);

  const handleEstimate = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.post(
        "/api/estimation",
        { farmSize, cropType, location },
        {
          headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
        }
      );
      setEstimate(data);
    } catch (error) {
      console.error("Error creating estimate", error);
    }
  };

  return (
    <div className="estimation-container">
      {/* Page Title */}
      <header className="estimation-header">
        <h1>Farm Cost Estimation</h1>
        <p>
          Plan your farming journey efficiently. Enter your farm details below
          to get an accurate cost estimate and expert advice tailored to your
          crop type and location.
        </p>
      </header>

      {/* Estimation Form */}
      <form className="estimation-form" onSubmit={handleEstimate}>
        <label htmlFor="farmSize">Farm Size (hectares)</label>
        <input
          type="number"
          id="farmSize"
          placeholder="e.g., 5"
          value={farmSize}
          onChange={(e) => setFarmSize(e.target.value)}
          required
        />

        <label htmlFor="cropType">Crop Type</label>
        <input
          type="text"
          id="cropType"
          placeholder="e.g., Maize"
          value={cropType}
          onChange={(e) => setCropType(e.target.value)}
          required
        />

        <label htmlFor="location">Location</label>
        <input
          type="text"
          id="location"
          placeholder="e.g., Lagos, Nigeria"
          value={location}
          onChange={(e) => setLocation(e.target.value)}
          required
        />

        <button type="submit" className="cta-button">
          Get My Estimate
        </button>
      </form>

      {/* Estimate Display */}
      {estimate && (
        <div className="estimate-card">
          <h2>Estimate Summary</h2>
          <p>
            <strong>Total Cost:</strong> ${estimate.totalCost}
          </p>
          <p>
            <strong>Advice:</strong> {estimate.advice}
          </p>
          <button className="cta-button-secondary">
            Get Started with Our Services
          </button>
        </div>
      )}
    </div>
  );
};

export default Estimation;
