import { useState, useEffect } from "react";
import axios from "axios";
import "../styles/BlogUploadEdit.css"; // External stylesheet for better styling

const BlogUploadEdit = () => {
  const [blogs, setBlogs] = useState([]);
  const [blog, setBlog] = useState({ title: "", content: "" });
  const [editMode, setEditMode] = useState(false);
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(false);

  const fetchBlogs = async () => {
    setLoading(true);
    try {
      const { data } = await axios.get("/api/admin/blogs");
      setBlogs(data);
    } catch (err) {
      console.error("Error fetching blogs", err);
    } finally {
      setLoading(false);
    }
  };

  const handleBlogSubmit = async (e) => {
    e.preventDefault();
    if (!blog.title.trim() || !blog.content.trim()) {
      setMessage("Both fields are required.");
      return;
    }
    try {
      if (editMode) {
        await axios.put(`/api/admin/blogs/${blog._id}`, blog, {
          headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
        });
        setMessage("Blog updated successfully!");
      } else {
        await axios.post("/api/admin/blogs", blog, {
          headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
        });
        setMessage("Blog added successfully!");
      }
      setBlog({ title: "", content: "" });
      setEditMode(false);
      fetchBlogs();
    } catch (err) {
      console.error("Error saving blog", err);
      setMessage("An error occurred while saving the blog.");
    }
  };

  const handleEdit = (blog) => {
    setBlog(blog);
    setEditMode(true);
    setMessage(""); // Clear messages when editing starts
  };

  const handleCancelEdit = () => {
    setBlog({ title: "", content: "" });
    setEditMode(false);
    setMessage(""); // Clear messages on cancel
  };

  const handleDelete = async (id) => {
    setLoading(true);
    try {
      await axios.delete(`/api/admin/blogs/${id}`, {
        headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
      });
      setMessage("Blog deleted successfully!");
      fetchBlogs();
    } catch (err) {
      console.error("Error deleting blog", err);
      setMessage("An error occurred while deleting the blog.");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchBlogs();
  }, []);

  return (
    <div className="container1">
      <h2>{editMode ? "Edit Blog" : "Upload Blog"}</h2>
      {message && <p className="message">{message}</p>}
      <form onSubmit={handleBlogSubmit} className="blog-form">
        <input
          type="text"
          placeholder="Title"
          value={blog.title}
          onChange={(e) => setBlog({ ...blog, title: e.target.value })}
          required
        />
        <textarea
          placeholder="Content"
          value={blog.content}
          onChange={(e) => setBlog({ ...blog, content: e.target.value })}
          required
        />
        <div className="form-buttons">
          <button type="submit">{editMode ? "Update Blog" : "Add Blog"}</button>
          {editMode && (
            <button
              type="button"
              onClick={handleCancelEdit}
              className="cancel-button"
            >
              Cancel
            </button>
          )}
        </div>
      </form>
      <h3>Existing Blogs</h3>
      {loading ? (
        <p>Loading...</p>
      ) : (
        <div className="blogs-list">
          {blogs.map((b) => (
            <div key={b._id} className="card">
              <h3>{b.title}</h3>
              <p>{b.content}</p>
              <div className="card-buttons">
                <button onClick={() => handleEdit(b)} className="edit-button">
                  Edit
                </button>
                <button
                  onClick={() => handleDelete(b._id)}
                  className="delete-button"
                >
                  Delete
                </button>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default BlogUploadEdit;
